.App {
  text-align: center;
}

.App-logo {
  height: 20vmin;
  pointer-events: none;
}

.App-header {
  background-color: #43a7dd;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.button{
  border: 0.3vmin solid #FFFFFF;
  border-radius: 2vmin;
  font-size: calc(10px + 0.7vmin);
  color: #FFFFFF;
  height: 7vmin;
  width: 15vmin;
  background-color: #43a7dd;
  cursor: pointer;
}

.button:hover {
  background-color: #43a7ff;
}

.space{
    width:5vmin;
    display:inline-block;
}